.homes{
	grid-column:center-start/center-end;
	margin: 15rem 0;
	 display: grid;
	 grid-template-columns: repeat(auto-fit, minmax(25rem,1fr));
	 grid-gap: 7rem;
}

.home{
	background-color: $color-grey-light-1;

	display: grid;
	grid-template-columns:repeat(2,1fr);
	grid-row-gap:3.5rem;
	&__img{
		width:100%;
		grid-row:1/2;
		grid-column: 1/-1;
		z-index: 1;
	}

	&__like{
		grid-row:1/2;
		grid-column: 2/3;
		z-index: 2;
		height: 2.5rem;
		width: 2.5rem;
		justify-self:end;
		margin:1rem;
				filter: invert(77%) sepia(9%) saturate(2215%) hue-rotate(347deg) brightness(85%) contrast(79%);
		
	}

	&__name{
	grid-row:1/2;
	grid-column: 1/-1;
	justify-self:center;
	z-index: 3;
	align-self: end;
	width: 80%;
	font-family:$font-display;
	font-size:1.6rem;
	text-align: center;
	padding:1.25rem;
	background-color: $color-secondary;
	color:#fff;
	font-weight: 400;
	transform: translateY(50%);

	}

	&__location,
	&__rooms{
		margin-top:2.5rem;
	}

	&__location,
	&__rooms,
	&__area,
	&__price{
			font-size:1.5rem;
			margin-left: 2rem;

			display: flex;
			align-items: center;

			img{
				filter: invert(77%) sepia(9%) saturate(2215%) hue-rotate(347deg) brightness(85%) contrast(79%);
				margin-right: 1rem;
			}
	}

	&__btn{
		grid-column: 1/-1;
	}
}