.header{
	background-color: $color-grey-dark-1;
	grid-column: full-start/col-end 6;
	background-image:linear-gradient(rgba($color-secondary,.93),rgba($color-secondary,.93)),url(../img/hero.jpeg);
	background-size: cover;
	background-position: center;
	padding:8rem;
	padding-top: 4rem;

	display: grid;
	grid-template-rows:1fr min-content min-max(6rem, min-content) 1fr;
	grid-template-columns: minmax(min-content,max-content);
	grid-row-gap: 1.5rem;
	justify-content: center;

	&__logo-box{
		display: flex;
		justify-content: center;
	}	
	&__heading{
		align-self: center;
		padding-left:1rem;
		font-size: 2rem;
	color:$color-primary;
	text-transform: uppercase;
	}

	&__logo{
		height: 5rem;
		justify-self:center;
		filter: invert(77%) sepia(9%) saturate(2215%) hue-rotate(347deg) brightness(85%) contrast(79%);
	}

	&__btn{
		align-self: start;
		justify-self:start;

	}

	&__seenon-text{
		display: grid;
		grid-template-columns: 1fr max-content 1fr;
		grid-column-gap:1.5rem;
		align-items: center;

		font-size:1.6rem;
		color:$color-grey-light-2;

		&::before,
		&::after{
			content: "";
			height: 1px;
			display: block;
			background-color: currentColor;
		}
	}

	&__seenon-logos {
		display: grid;
		grid-template-columns: repeat(4,1fr);
		grid-column-gap: 3rem;
		justify-content: center;
		align-items: center;
		justify-items:center;

	img{
		max-height: 2.5rem;
			max-width:100%;
			filter: brightness(70%);
	}


	}

}